* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: white;
  transition: 2s;
}

body {
  height: 100vh;
  width: 100%;
  background-image: url(../img/IMG_5363.JPG);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  backdrop-filter: blur(3px);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.header {
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-transform: uppercase;
  margin-top: 30px;
}
.header .logo-square {
  width: 250px;
  height: 250px;
  border: 5px solid white;
  margin-bottom: 20px;
}
.header .logo h1 {
  font-size: 3rem;
  position: relative;
  font-weight: 300;
}
.header .logo h1 span {
  letter-spacing: 14px;
}
.header .logo p {
  font-size: 0.8rem;
}

.main {
  width: 100%;
  padding: 10px;
}

.content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  justify-content: space-around;
  position: relative;
  top: 0;
}

.container {
  display: flex;
  justify-content: center;
}

.link-instagram {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 20px;
  border: 2px solid rgba(255, 255, 255, 0.9);
  background-image: url(../img/InstagramPNG3.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.link-facebook {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 20px;
  border: 2px solid rgba(255, 255, 255, 0.9);
  background-image: url(../img/facebook.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.link-booksy {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 20px;
  border: 2px solid rgba(255, 255, 255, 0.9);
  background-image: url(../img/booksy.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.link-maps {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 20px;
  border: 2px solid rgba(255, 255, 255, 0.9);
  background-image: url(../img/icons8-google-maps-240.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-color: white;
}

.footer {
  opacity: 0;
  color: #f8f8f8;
  padding: 20px;
  gap: 20px;
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer .phone {
  touch-action: none;
}

@keyframes opasityBox {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.25;
  }
  50% {
    opacity: 0.5;
  }
  75% {
    opacity: 0.75;
  }
  100% {
    opacity: 1;
  }
}
@keyframes movingBox {
  0% {
    top: 100px;
  }
  25% {
    top: 75px;
  }
  50% {
    top: 50px;
  }
  75% {
    top: 25px;
  }
  100% {
    top: 0px;
  }
}
.glow {
  animation-name: opasityBox;
  animation-iteration-count: 1;
  animation-duration: 1500ms;
}

.move {
  animation-name: movingBox;
  animation-iteration-count: 1;
  animation-duration: 1500ms;
}